@import url(https://fonts.googleapis.com/css?family=Rubik:300,400,500,700,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Rubik&display=swap);
@import url(https://fonts.googleapis.com/css?family=Rubik:300,400,500,700,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.containerLogin{
    margin-left: 30rem;
    font-family: 'Rubik', sans-serif;
    height: 100vh;
    width: 35vw;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    box-shadow: 0px 0px 20px #0000000D;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.login{
    margin-top: 4rem;
    font-weight: 700;
    font-size: 2.2rem;
    color: #191919;
    margin-bottom: -5rem;
}

.para{
    color: #191919;
    font-size: 1.2rem;
    margin: 0;
}

.forgotPassword{
    margin-top: -0.3rem;
    margin-left: 0.4rem;
    letter-spacing: 1.2px;
    color: #0060FC;
    text-transform: uppercase;
    font-size: 0.6rem;
    font-weight: 600;
}

.input{
    width: 20vw;
    height: 5vh;
    background: #F4F4F4;
    border-radius: 7px;
    text-decoration: none;
    border: none;
    margin-bottom: 13px;
    padding-left: 25px;
}

.input::-webkit-input-placeholder{
    font-weight: 400;
    margin-left: 15px;
    font-size: 1rem;
    color: #A3A3A3;
}

.input:-ms-input-placeholder{
    font-weight: 400;
    margin-left: 15px;
    font-size: 1rem;
    color: #A3A3A3;
}

.input::-ms-input-placeholder{
    font-weight: 400;
    margin-left: 15px;
    font-size: 1rem;
    color: #A3A3A3;
}

.input::placeholder{
    font-weight: 400;
    margin-left: 15px;
    font-size: 1rem;
    color: #A3A3A3;
}

.button{
    float: right;
}

p {

    margin-top: -8rem;
}

#UserName {

    margin-top: -15rem;
}

#link {

    margin-left: 3.5rem;
    margin-bottom: 10rem;
}
.Button{
    text-decoration: none;
    background: #1744FF 0% 0% no-repeat;
    cursor: pointer;
    box-shadow: 0px 0px 10px #0000000D;
    border-radius: 8px;
    padding: 1rem 1.4rem;
    letter-spacing: 2.1px;
    color: #FFFFFF;
    text-transform: uppercase;
    font-size: 13px;
    font-family: 'Rubik', sans-serif;
    border: none;
}
#welcomeUser {
    height: 100px;
    background-color: dodgerblue; 
    margin-top: 0rem; 
    display: flex;
    font-family: 'Rubik', sans-serif;
    color: bisque;
    justify-content: space-between;
    line-height: 100px;
}

#logout {
    float: right;
    margin-right: 1rem;
    line-height: inherit;
    margin-top: 2rem;
    cursor: pointer;
}

img:hover {
    box-shadow: 0 0 2px 1px rgba(2, 140, 186, 2);
}
